.landing-container{
    height: 100%;
    width: 100%;
    display: flex;
}
.landing-left{
    width: 60%;
    background-image: url("./../../../img/oval.svg");
    background-position: center;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-size: cover;
}
.landing-left-element1{
    font-family: Helvetica;
    font-size: 52px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #f6f6f6;
    padding-top: 4vh;
}
.landing-left-talpalogo{
    text-align: center;
    padding-top: 30vh;
}
.landing-left-element2{
    font-family: Helvetica;
    font-size: 64px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #f6f6f6;
}
.landing-right{
    width: 40%;
}
.landing-button-wrapper{
    padding-top: 40vh;
    text-align: center;
    width: 100%;
}
.landing-right-element{
    font-family: Helvetica;
    font-size: 18px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #616161;
    padding-top: 30px;
}
.icon-wrapper{
    display: flex;
    width: 65%;
    padding-top: 25%;
    margin: auto;
}
.landing-logo-content{
    font-family: Helvetica;
    font-size: 14px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #616161;
    padding-right: 14px;
    padding-top: 23px;
}
