html.body{
	margin: 0px;
	padding: 0px;
}
.wrapper{
    position: absolute;
    margin-top: -6%;
    background: white;
    width: 240px;
    box-shadow: 2px 2px 8px 0 rgba(0,0,0,0.14);
    z-index: 999999;
    margin-left: -30px;
}
.header{
    font-size: 12px;
	height: 40px;
    width: 100%;
    display: flex;
    /* border: 1px solid #eee; */
}
.header-wrapper{
    height: 40px;
    margin: auto;
}
/* .previous-month{
	float: left;
	height: 100%;
	width: 40px;
    background-color: #fff;
    border: 1px solid #eee;
} */
.display-month{
    width: 154px;
    font-family: Helvetica;
    font-size: 14px;
    color: #616161;
    letter-spacing: 1.05px;
    line-height: 15px;
    padding: 10px;
    text-align: center;
}
.previous-month, .next-month{
	height: 100%;
    background-color: #fff;
    cursor: pointer;
    padding: 10px;
    padding-left: 13px;
    box-sizing: border-box;
}
.days-row{
    font-size: 12px;
    font-family: Helvetica;
    color: #979797;
    display: flex;
    padding-top: 5px;
}
.day-cell{
	width: 29px;
    padding-left: 5px;
}

.date-wrapper{
    width: 100%;
    padding-top: 5px;
}
.date-cell{
	height: 31px;
    width: 31px;
    display: inline-block;
    font-size: 14px;
    cursor: pointer;
    font-family: Helvetica;
    color: #616161;
    letter-spacing: 1.05px;
    line-height: 15px;
    padding-left: 2px;
    padding-top: 10px;
    text-align: center;
    /* margin: 2px; */
}  
.date-cell:hover{
    height: 31px;
    width: 31px;
    display: inline-block;
    font-size: 14px;
    cursor: pointer;
    font-family: Helvetica;
    letter-spacing: 1.05px;
    line-height: 15px;
    padding-top: 10px;
    text-align: center;
    background: #d1d9e8;
    color: #616161;
    /* border-radius: 100%; */
} 
.calender-div{
    position: absolute;
    z-index: 9999;
    background-color: #fff;
    margin-top: 35px;
}