.field {
    /* padding-top: 10px; */
    margin-top: -20px;
    display: flex;
    flex-direction: column;
  }
  label {
    order: -1;
    padding-left: 15px;
    transition: all 0.3s ease-in;
    transform: translateY(20px);
    pointer-events: none;
  }
  input:focus + label {
    transform: translateY(-2px);
    /* padding-top: 10px;
    display: flex;
    flex-direction: column; */
  }
.input-icon{
    position: absolute;
    margin-top: 11px;
    margin-left: -30px;
    z-index: 99;
}